<template>
  <div class="fill-height">
    <app-bar title="Contas">
      <template v-slot:append>
        <v-btn icon to="/" replace>
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </template>
    </app-bar>
    <v-card class="ma-2">
      <template v-for="conta in contas">
        <v-list-item
          :key="conta.id"
          :disabled="conta.regional === $store.getters['session/payload'].regional"
          style="border-bottom: 1px solid rgba(0,0,0,.2)"
          @click="alternarConta(conta.regional)"
        >
          <v-list-item-content>
            <v-list-item-title>
              CRECI/{{getRegional(conta.regional, false)}}
              <v-chip color="primary" x-small label v-if="conta.regional === $store.getters['session/payload'].regional">ATUAL</v-chip>
            </v-list-item-title>
            <v-list-item-subtitle>
              {{conta.regional}}ª REGIÃO - {{getRegional(conta.regional, true)}}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-card>
  </div>
</template>

<script>
import AppBar from '@/components/AppBar';
import CreciHelper from '@/helpers/CreciHelper';
import SessionWebClient from '@/http/SessionWebClient';
export default {
  name: 'Contas',
  components: {AppBar},
  computed: {
    contas() {
      return this.$store.state.ficha.cadastro.contas;
    }
  },
  methods: {
    getRegional(regiao, extenso) {
      return CreciHelper.idParaUf(regiao, extenso, true);
    },
    async alternarConta(regiao) {
      const sessionWebClient = new SessionWebClient();
      this.$loading(true);
      try {
        const token = await sessionWebClient.createSession(regiao, this.$store.getters["session/payload"].cpf);
        this.$store.commit('session/setToken', token);
        await this.$store.dispatch('ficha/reloadAll');
        await this.$router.push('/');
      } finally {
        this.$loading(false);
      }
    }
  },
}
</script>

